@import "../../_globalColor";

.dev-icons {
  padding-left: 0;
  list-style: none;
  font-size: 3rem;
  margin-bottom: 0px;
  margin-top: 0px;
  text-align: center;
}

.software-skill-inline {
  display: inline-block;
  margin-right: 20px;
  margin-bottom: 20px;
}

.software-skill-inline > i {
  color: $subTitle;
}

.software-skill-inline > i:hover {
  color: $skillsColor;
}
.software-skill-inline > p {
  color: $subTitle;
  font-size: 10px;
}
.software-skill-inline > i:hover ~ p {
  color: $skillsColor;
}

.box-outer{
  border-radius: 10px;
  padding: 20px;
  box-shadow: -15px 0 10px rgba(162, 161, 161, 0.5), 15px 0 10px rgba(162, 161, 161, 0.5) ;
}


.d-flex{
  display: flex;
}

h6{
font-family: "Montserrat";
}

@media (max-width: 768px) {
  .d-flex{
    flex-wrap: wrap;
    justify-content: center;
  }
}
